.team {

    &__container {
    }

    &__title {
        
    }

    &__controls {
        display: flex;
        justify-content: space-between;
        align-items:center;
        padding: 0px 0px #{fluid(32px, 48px, 320px, 1920px)} 0px;
    }

    &__text {
        max-width: 700px;
    }

    &__navigation {
        display: flex;
        gap: 32px;
    }

    &__button-prev {
    }

    &__button-next {
    }

    &__swiper {
    }

    &__columns {
        display: flex;
        margin: 0px 0px 16px 0px;
    }

    &__column {
        width: calc(25% - 16px);
        @include tablet{
            width: calc(33.33333% - 16px);
        }
        @include small-tablet{
            width: calc(50% - 16px);
        }
        @include mobile{
            width: calc(100% - 16px);
        }
    }

    &__image {
        margin: 0px 0px 8px 0px;
    }

    &__content {
    }

    &__job {
        font-size: #{fluid(14px, 16px, 320px, 1920px)};
        color: var(--dark-60);
        margin: 0px 0px 8px 0px;
    }

    &__name {
        font-weight: 600;
    }
}
